.productArea{
    white-space: nowrap;
    display: -moz-grid;
    display: -ms-grid;
    display: grid;
    grid-template-columns: 250px 892px 300px;
}

.productArea>div{
    display: inline-block;
}

/* 섬네일 */
.productThumbImg{
    grid-column-start: 1;
    grid-column-end: 2;
}
.productThumbImg>ul{
    margin-top: 150px;
    display: inline-block;
    width: 200px;
    text-align: center;
    
}
.productThumbImg li{
    width: 152px;
    list-style: none;
    box-sizing: border-box;
    padding: 0px;
    margin-bottom: 10px
}
.productThumbImg li:hover{
    cursor: pointer;
    border: 1px #ccc solid;
    border-radius: 10px;
}
.productThumbImg li>img{
    width: 150px;
    padding: 0;
}
/* 섬네일 */
/* 제품이미지 */
.showImg{
    width: 892px;
    height: 684px;
    overflow: hidden;
    grid-column-start: 2;
    grid-column-end: 3;
}
.showImg>.slider{
    position: relative;
    transition: all 0.3s;
    left: 0;
}
/* 제품이미지 */

/* 용량선택 */
.descProduct{
    position: relative;
    grid-column-start: 3;
    grid-column-end: 4;
    margin-top: 150px;
    margin-left: 30px;
    background-color: #578bf2;
    border-radius: 20px;
    padding: 20px;
    height: 400px;
    text-align: left;
    overflow: hidden;
    box-shadow: 3px 3px 1px #ccc;
}
.descProduct>h2{
    color: #fff;
    
}
.descProduct>h3{
    margin-top: -15px;
    margin-left: 3px;
    color: #fff;
    font-weight: lighter;
}
.chooseCap{
    
}
.chooseCap>ul{
    margin-top: 40px;
    padding: 0;
    text-align: center;
}
.chooseCap li{
    list-style: none;
    margin-bottom: 10px;
}
.chooseCap li>input{
    display: none;
}
.chooseCap li>label{
    display: inline-block;
    width: 100px;
    height: 50px;
    background-color: #fff;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    border-radius: 5px;
}
.chooseCap li>input:checked+label{
    background-color: antiquewhite;
}

.descProduct span{
    position: relative;
    display: inline-block;
    left: -5px;
    width: 240px;
    top: 40px;
    margin: 0 auto;
}
.descProduct span>a{
    color: #101010;
    text-align: center;
    display: inline-block;
    width: 200px;
    text-decoration: none;
    border-radius: 15px;
    padding: 20px;
    background-color: antiquewhite;
}
/* 용량선택 */


