.moreSpecArea>h2{
    width: 100%;
    text-align: left;
    background-color: black;
    color: #fff;
    padding: 20px;
}
.moreSpecArea table{
    width: 100%;
    text-align: center;
    border: none;
}
.moreSpecArea table td{
    padding: 20px;
}
.moreSpecArea table tr:first-child td{
    font-weight: bold;
    font-size: 15pt;
}
.moreSpecArea table td[rowspan]{
    font-weight: bold;
    font-size: 15pt;
}
.moreSpecArea table td{
    border-bottom: 1px #ccc solid;
}

.moreSpecArea table td[rowspan="2"]~td{
    border-bottom: none;
    font-weight: bold;
    border-top:  1px #ccc solid;
    padding-bottom: 0;
}