.buyProductArea>h2{
    width: 100%;
    text-align: left;
    background-color: black;
    color: #fff;
    padding: 20px;
}
.buyProductArea>h2+p{
    position: relative;
    top: -10px;
    text-indent: 20px;
}
.buyCap{
    border:none;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    padding: 20px;
    padding-bottom: 10px;
    margin-bottom: 50px;
}
.buyCap legend{
    font-weight: bold;
    padding: 0 10px 0 10px;
}
.buyCap input{
    padding: 10px;
    margin-bottom: 20px;
}
.buyCap label{
    margin-left: 10px;
    font-size: larger;
}

.buyInfo{
    border:none;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    padding: 20px;
    padding-bottom: 10px;
    margin-bottom: 20px;
}
.buyInfo legend{
    font-weight: bold;
    padding: 0 10px 0 10px;
}
.buyInfo input{
    margin-bottom: 30px;
    border: none;
    background-color: #f9f9f9;
    padding: 2px;
}
.buyInfo label{
    display: inline-block;
    width: 100px;
}

.buyInfo+input{
    display: block;
    border: none;
    border-radius: 10px;
    width: 100px;
    height: 50px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 100px;
    background-color: #021e73;
    color: #fff;
}