#header{
    position: fixed;
    top: 150px;
    left: -40px;
    z-index: 100;
}
#header li{
    list-style: none;
    padding: 15px;
    background-color: #021e73;
    text-align: center;
    cursor: pointer;
    
}
#header li:first-child{
    border-radius: 0 10px 0 0;
}
#header li:last-child{
    border-radius: 0 0 10px 0;
}
#header li:hover{
    background-color:  #91b2f2;
}
#header li>a{
    text-decoration: none;
    color: #fff;
    padding: 20px;
    font-style: none;
}
#header li>a:focus{
    outline:none;
}